.header {
    display: flex;
    flex-direction: row;
    background-image: linear-gradient(var(--primary),var(--primary),var(--primary),var(--primary),#41414300) ;
    align-items: center;
    flex-wrap: wrap;
}

.header .left {
    flex:25%;
}

.header .middle {
    flex:50%
}

.header .right {
    flex:25%;
    
}


.header > .button {
    background-image: url(../../assets/silver-name-plate.png);
    background-size: cover;
}